/* eslint-disable react/no-unescaped-entities */
import React, { useState } from 'react';
import { useFormik } from 'formik';
import { useNavigate } from 'react-router-dom';
import { Box, Grid, InputAdornment, Typography, Button } from '@mui/material/';

//3rd party
import { EyeOutlined, EyeInvisibleOutlined } from '@ant-design/icons';

//project imports
import MuiTextField from '../../../components/TextField';

const Form = () => {
  const [capsWarning, setCapsWarning] = useState(false);
  const [showPassword, setShowPassword] = useState(false);

  const handleClickShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const onKeyDown = (keyEvent) => {
    if (keyEvent.getModifierState('CapsLock')) {
      setCapsWarning(true);
    } else {
      setCapsWarning(false);
    }
  };
  const navigate = useNavigate();
  const formik = useFormik({
    validateOnBlur: false,
    validateOnChange: false,
    initialValues: {
      id: '',
      firstname: '',
      lastname: '',
      dob: '',
      email: '',
      phone: '',
      password: '',
      confirmPassword: ''
    },
    validate: (values) => {
      let errors = {};
      if (values.id.trim() === '') {
        errors.id = 'G Pace ID required. This is on your plastic ID card';
      }
      if (values.firstname.trim() === '') {
        errors.firstname = 'Firstname required';
      }
      if (values.lastname.trim() === '') {
        errors.lastname = 'Lastname required';
      }
      if (values.dob.trim() === '') {
        errors.dob = 'DOB required';
      }
      if (values.phone.trim() === '') {
        errors.phone = 'Mobile number required';
      }
      if (values.email.trim() === '') {
        errors.email = 'Email address required';
      }
      if (values.password.trim() === '') {
        errors.password = 'Password required for account set up';
      }
      if (values.confirmPassword.trim() === '') {
        errors.confirmPassword = 'You must confirm your password';
      }
      return errors;
    },
    onSubmit: (values) => {
      console.log(values);
      navigate('/record-enquiry');
    }
  });
  return (
    <Box>
      <Box marginBottom={4}>
        <Typography
          variant="h4"
          sx={{
            fontWeight: 700
          }}
        >
          Register with HRI PLUS today
        </Typography>
        <Typography color="text.secondary">*Please use G Pace ID and DOB supplied on ID card issued to you *</Typography>
      </Box>
      <form onSubmit={formik.handleSubmit}>
        <Grid container spacing={2}>
          <Grid item xs={12} md={6}>
            <MuiTextField
              autofocus
              label="G Pace ID"
              id="id"
              type="tel"
              name="id"
              onChange={formik.handleChange}
              value={formik.values.id}
              error={Boolean(formik.errors.id)}
              helperText={formik.errors.id}
            />
          </Grid>
        </Grid>
        <Grid sx={{ mt: 0.2 }} container spacing={2}>
          <Grid item xs={12} md={6}>
            <MuiTextField
              autofocus
              label="First Name"
              id="firstname"
              type="text"
              name="firstname"
              onChange={formik.handleChange}
              value={formik.values.firstname}
              error={Boolean(formik.errors.firstname)}
              helperText={formik.errors.firstname}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <MuiTextField
              label="last Name"
              id="lastname"
              type="number"
              name="lastname"
              onChange={formik.handleChange}
              value={formik.values.lastname}
              error={Boolean(formik.errors.lastname)}
              helperText={formik.errors.lastname}
            />
          </Grid>

          <Grid item xs={12} md={6}>
            <MuiTextField
              label="Mobile Number"
              id="phone"
              type="number"
              name="phone"
              onChange={formik.handleChange}
              value={formik.values.phone}
              error={Boolean(formik.errors.phone)}
              helperText={formik.errors.phone}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <MuiTextField
              label="Email Address"
              id="email"
              type="email"
              name="email"
              onChange={formik.handleChange}
              value={formik.values.email}
              error={Boolean(formik.errors.email)}
              helperText={formik.errors.email}
            />
          </Grid>
          <Grid item xs={12}>
            <MuiTextField
              label="DOB"
              id="dob"
              type="date"
              name="dob"
              onChange={formik.handleChange}
              value={formik.values.dob}
              error={Boolean(formik.errors.dob)}
              helperText={formik.errors.dob}
            />
          </Grid>

          <Grid item xs={12}>
            <MuiTextField
              name="password"
              label="Password"
              color={capsWarning ? 'warning' : 'primary'}
              id="-password-login"
              type={showPassword ? 'text' : 'password'}
              onChange={formik.handleChange}
              value={formik.values.password}
              onBlur={(event) => {
                setCapsWarning(false);
              }}
              error={Boolean(formik.errors.password)}
              helperText={formik.errors.password}
              onKeyDown={onKeyDown}
              InputProps={{
                endAdornment: (
                  <InputAdornment sx={{ cursor: 'pointer' }} onClick={handleClickShowPassword} position="end">
                    {showPassword ? <EyeOutlined /> : <EyeInvisibleOutlined />}
                  </InputAdornment>
                )
              }}
            />
            {capsWarning && !formik.errors.password && (
              <Typography variant="caption" sx={{ color: 'warning.main' }} id="warning-helper-text-password-login">
                Caps lock on!
              </Typography>
            )}
          </Grid>

          <Grid item xs={12}>
            <MuiTextField
              name="confirmPassword"
              label="Confirm Password"
              color={capsWarning ? 'warning' : 'primary'}
              id="confirmPassword"
              type={showPassword ? 'text' : 'password'}
              onChange={formik.handleChange}
              value={formik.values.confirmPassword}
              onBlur={(event) => {
                setCapsWarning(false);
              }}
              error={Boolean(formik.errors.confirmPassword)}
              helperText={formik.errors.confirmPassword}
              onKeyDown={onKeyDown}
              InputProps={{
                endAdornment: (
                  <InputAdornment sx={{ cursor: 'pointer' }} onClick={handleClickShowPassword} position="end">
                    {showPassword ? <EyeOutlined /> : <EyeInvisibleOutlined />}
                  </InputAdornment>
                )
              }}
            />
            {capsWarning && !formik.errors.password && (
              <Typography variant="caption" sx={{ color: 'warning.main' }} id="warning-helper-text-password-login">
                Caps lock on!
              </Typography>
            )}
          </Grid>

          <Grid item container xs={12}>
            <Box
              display="flex"
              flexDirection={{ xs: 'column', sm: 'row' }}
              alignItems={{ xs: 'stretched', sm: 'center' }}
              justifyContent={'space-between'}
              width={1}
              maxWidth={600}
              margin={'0 auto'}
            >
              <Button size={'large'} variant={'contained'} type={'submit'}>
                Proceed to payment
              </Button>
            </Box>
          </Grid>
        </Grid>
      </form>
    </Box>
  );
};

export default Form;
